.movie-list-container {
    display: flex;
    flex-direction: column;
    grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
    gap: 24px;
    max-width: 1010px;
    margin: 0 auto;
    min-height: 80hv;
    justify-content: space-between;
}

.movie-page-wrapper {
    max-width: 1010px;
    margin: 0 auto;
    padding: 24px;
}

.movie-card {
    display: flex;
    width: 100%;
    height: 279px;
    justify-content: space-between;
    align-items: flex-start;
  }
  
  .movie-image {
    height: 100%;
    object-fit: cover;
    border-radius: 4px;
    width: auto;
    display: block;
  }
  
  .movie-placeholder {
    width: auto;
    height: 100%;
    background: #f0f0f0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    color: #999;
    font-size: 14px;
    margin-right: 16px;
  }

  .movie-content {
    margin-left: 16px;
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 15px;
  }
  
  .movie-content h3 {
    margin-bottom: 4px;
    margin-right: 43px;
  }
  
  .movie-date {
    font-size: 12px;
    color: #999;
    margin-bottom: 8px;
  }
  
  .movie-tags span {
    background-color: #f0f0f0;
    padding: 2px 6px;
    border-radius: 4px;
    font-size: 12px;
    margin-right: 6px;
  }
  
  .movie-description {
    white-space: normal;
    font-size: 13px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .ant-col {
    display: flex;
    justify-content: center;
  }

  :where(.css-dev-only-do-not-override-1261szd).ant-card .ant-card-body {
    padding: 0;
  }

  .movie-page-wrapper {
    padding: 13px;
    max-width: 1200px;
    margin: 0 auto;
  }

  .rating-badge {
    position: absolute;
    top: 8px;
    right: 8px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0);
    width: 20px;
    height: 25px;
    padding: 4px 8px;
    font-size: 14px;
    z-index: 1;
  }

  .ant-rate {
    position: absolute;
    bottom: 25px;
    font-size: 17px;
  }

